.nav {
  &__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    display: flex;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 12px;
    margin-left: 20px;

    &:first-child {
      margin-left: 0;
    }
  }

  &__link {
    text-decoration: none;
    color: $color-main_accent;
    line-height: 60px;
    position: relative;

    &:hover {
      color: $color-blue_accent;
    }
  }

  .is-active {
    color: $color-blue_accent;
  }

  .is-active::after {
    content: "";
    position: absolute;
    height: 4px;
    background: $color-blue_accent;
    border-radius: 8px;
    bottom: 0;
    left: 0;
    right: 0;
  }
}
